/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"It's All True","date":"30 May 2001","slug":"/2001-05-30-its-all-true/","tags":"Y2001"},"html":"<p><b>Last Flight Home</b></p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/goodnite.jpg\" height=\"220\" width=\"300\" border=\"0\" alt=\"goodnite.jpg: \" /></p>\n<p>Thanks to an overly long meeting in Munich, Mama missed the last flight back to Hamburg. She has to fly to Hannover and rent a car, and she could only say good night to Christopher on the phone.</p>\n<p><b>Culinary highlights of Amsterdam</b></p>\n<ul>\n<li>Like <a href=\"http://scriptingnews.userland.com/backissues/2001/05/26\">Dave</a>, we had to have herring Broodjes. They were good, and Christopher enjoyed his share, but we didn't think they much different from north German Fischbrotchen.\n</li>\n<li>On Sunday we three shared a large order of Poffertjes (mini pancakes) with strawberries and cream for lunch. We sat outside at a colorful stand.\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/poffertjes1.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"poffertjes1.jpg: \" /> </p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/poffertjes2.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"poffertjes2.jpg: \" /> </p>\n</li>\n<li>And, of course, the beer. Thankfully, the north German '7 minute Pils' never caught on in Holland, and your Heineken (short but fresh) is brought to you within seconds after it's ordered.\n</li>\n</ul>"},"next":{"fields":{"title":"Nice Life","date":"29 May 2001","slug":"/2001-05-29-nice-life/"}},"previous":{"fields":{"title":"Goodbye Mr. Anderson","date":"03 Jun 2001","slug":"/2001-06-03-goodbye-mr-anderson/"}}},{"node":{"fields":{"title":"Nice Life","date":"29 May 2001","slug":"/2001-05-29-nice-life/","tags":"Y2001"},"html":"<p><b>Amsterdam with a small child</b></p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/a.gif\" height=\"300\" width=\"202\" border=\"0\" alt=\"a.gif: \" /></p>\n<p>That's <a href=\"http://www.spicynoodles.com\">Andr&eacute;</a> and <a href=\"http://andrea.editthispage.com/\">Andrea</a> making sure that Christopher doesn't escape from his stroller. (Actually, when I looked closer at the picture, I noticed that at that very moment they were not watching Christopher, but instead <a href=\"http://shanson.editthispage.com/pictures/viewer$609\">they were being beamed out</a> of the <a href=\"http://www.whatisthematrix.com/\">Matrix</a>!)</p>\n<p>It was fun to watch Christopher in the city. Amsterdam is a city of narrow, busy streets, and he seemed to be fascinated by all the movement so close to him. And he loved the boats on the canals. At every bridge, he had to watch any boats until they were out of sight. So we felt it our duty to take him on a canal cruise as well.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/canal.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"canal.jpg: \" /></p>\n<p>The weekend with him was easier than I expected. I was afraid that he would have problems adjusting to the travel and new environment, but as it was, he adjusted better than either of his parents. We usually plan for Christopher to take about half of our free time on vacation, but I would say this weekend that he took only a third of our time, so we had more free time that I thought we would. But our hands were full enough that we didn't take very many pictures. </p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/hotellobby2.jpg\" height=\"300\" width=\"225\" border=\"0\" alt=\"hotellobby2.jpg: \" /></p>\n<p>Of course, we need to be able to regularly change diapers. We had no idea where we could find changing rooms, especially during the 3 hours while we were waiting to check into our room, so twice we ended up going to our car simply to change Christopher on the back seat.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/hotellobby1.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"hotellobby1.jpg: \" /></p>\n<p>In the hotel room Christopher felt like a king. He had room to move around, played with the telephone, bounced on the king-sized bed, and was able to use the leather chair as a slide.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/hotelroom.jpg\" height=\"300\" width=\"225\" border=\"0\" alt=\"hotelroom.jpg: \" /></p>"},"next":{"fields":{"title":"Letdown","date":"28 May 2001","slug":"/2001-05-28-letdown/"}},"previous":{"fields":{"title":"It's All True","date":"30 May 2001","slug":"/2001-05-30-its-all-true/"}}},{"node":{"fields":{"title":"Letdown","date":"28 May 2001","slug":"/2001-05-28-letdown/","tags":"Y2001"},"html":"<p>We made it back from Amsterdam, and we're still tired. Christopher slept 3 hours more than usual today, and he's not caught up with his sleep either. We're all kind of depressed today, the weather is rainy, and work is, well, work.</p>\n<p>I'll write up my notes under the story <a href=\"http://shanson.editthispage.com/stories/storyReader$600\">Amsterdam</a> until they are ready to post, so you can read them in progress (that way I have no pressure to get them done). </p>\n<p><b>Van Gogh</b></p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/05/vangogh.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"vangogh.jpg: \" /></p>\n<p>Here's Mama and Christopher in line at the <a href=\"http://www.vangoghmuseum.nl/\">Van Gogh Museum</a> on Sunday. Christopher can hardly wait for the dose of culture that he's about to be fed. His favorite painting was <a href=\"http://www.vangoghmuseum.nl/collection/catalog/vglpainting.asp?ARTID=1&LANGID=0&SEL=1&PERIOD=1&SORT=NaN\">The Potato Eaters</a>, for obvious reasons (he likes anything to do with eating). Mama and I both liked <a href=\"http://www.vangoghmuseum.nl/collection/catalog/vglpainting.asp?ARTID=86&LANGID=0&SEL=1&PERIOD=3&SORT=NaN\">The Langlois Bridge</a>, but it wasn't for sale. Not even as a print. </p>\n<p><img src=\"http://www.vangoghmuseum.nl/collection/catalog/smallpaintings/12_S027V.jpg\" /></p>"},"next":{"fields":{"title":"First Impressions from Amsterdam","date":"27 May 2001","slug":"/2001-05-27-first-impressions-from-amsterdam/"}},"previous":{"fields":{"title":"Nice Life","date":"29 May 2001","slug":"/2001-05-29-nice-life/"}}}],"pathPrefix":"","first":false,"last":false,"index":1000,"pageCount":1101}}

/***/ })

